  transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
}

.ease-in-out{
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

html {
  scroll-behavior: smooth;
}

.arrow-container {
  position: absolute;
  bottom: 0;
  right: 0;
}

.grid-item img {
  width: 100%;
}

.options-container {
  position: absolute;
  z-index: 50;
  top: 25px;
  right: -30px;
}

.open-status {
  background-color: #111111;
  color: white;
}

@media (max-width: 768px) {
  .options-container {
    top: 25px;
    left: 48%;
    transform: translateX(-48%);
  }
}

.ofert-container:hover .options-container {
  display: block;
}

.projects-grid {
  max-height: 220vh;
  overflow: hidden;
}

.mask {
  height: 50vh;
  width: 100%;
  background: linear-gradient(
    0deg,
    rgba(220, 193, 171, 1) 28%,
    rgba(0, 212, 255, 0) 100%
  );
  position: absolute;
  z-index: 10;
  bottom: 0;
}

.show-more-btn {
  position: absolute;
  z-index: 20;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

@media (max-width: 768px) {
  .show-more-btn {
    left: 40%;
  }
}

.dark-mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.grid-photos-modal {
  z-index: 50;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -70%);
  background-color: transparent;
  width: 60vh;
  height: 60vh;
}

.modal-left-btn {
  border: solid 3px #111111;
  position: absolute;
  top: 50%;
  left: -20px;
  width: 40px;
  border-radius: 50%;
  transition: box-shadow 0.15s;
  transform: translateY(-50%);
  background-color: white;
  height: 40px;
}

.modal-right-btn {
  border: solid 3px #111111;
  position: absolute;
  top: 50%;
  right: -20px;
  width: 40px;
  transition: box-shadow 0.15s;
  transform: translateY(-50%);
  border-radius: 50%;
  background-color: white;
  height: 40px;
}

.modal-left-btn:hover,
.modal-right-btn:hover {
  box-shadow: 0px 0px 0px 5px rgba(17, 17, 17, 1);
}

.close-modal-btn {
  border: solid 3px #1b5b31;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -170%);
  transition: box-shadow 0.15s;
  background-color: white;
}

.close-modal-btn:hover {
  box-shadow: 0px 0px 0px 5px rgba(27, 91, 49, 1);
}

/* hamburger menu */

.hamburger {
  cursor: pointer;
  width: 24px;
  height: 24px;
  transition: all 0.25s;
  position: relative;
}

.hamburger-top,
.hamburger-middle,
.hamburger-bottom {
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background: #000;
  transform: rotate(0);
  transition: all 0.5s;
}

.hamburger-middle {
  transform: translateY(7px);
}

.hamburger-bottom {
  transform: translateY(14px);
}

.open {
  transform: rotate(90deg);
  transform: translateY(0px);
}

.open .hamburger-top {
  transform: rotate(45deg) translateY(6px) translate(6px);
}

.open .hamburger-bottom {
  transform: rotate(-45deg) translateY(6px) translate(-6px);
}

.open .hamburger-middle {
  display: none;
}

