.modal-left-btn {
  background-color: #fff;
  border: 3px solid #111;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  transition: box-shadow .15s;
  position: absolute;
  top: 50%;
  left: -20px;
  transform: translateY(-50%);
}

.modal-right-btn {
  background-color: #fff;
  border: 3px solid #111;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  transition: box-shadow .15s;
  position: absolute;
  top: 50%;
  right: -20px;
  transform: translateY(-50%);
}

.modal-left-btn:hover, .modal-right-btn:hover {
  box-shadow: 0 0 0 5px #111;
}

.close-modal-btn {
  background-color: #fff;
  border: 3px solid #1b5b31;
  transition: box-shadow .15s;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -170%);
}

