.hamburger {
  cursor: pointer;
  width: 24px;
  height: 24px;
  transition: all .25s;
  position: relative;
}

.hamburger-top, .hamburger-middle, .hamburger-bottom {
  background: #000;
  width: 24px;
  height: 2px;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(0);
}

.hamburger-middle {
  transform: translateY(7px);
}

.hamburger-bottom {
  transform: translateY(14px);
}

.open {
  transform: translateY(0);
}

.open .hamburger-top {
  transform: rotate(45deg)translateY(6px)translate(6px);
}

.open .hamburger-bottom {
  transform: rotate(-45deg)translateY(6px)translate(-6px);
}

.open .hamburger-middle {
  display: none;
}

